.main-svg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

}

.action-buttons {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 0;
  z-index: 1;

  &__item {
    font-size: 14px;
    margin: 10px 5px;
    cursor: pointer;
    display: inline-block;
    user-select: none;
    padding: 10px;
    border-radius: 7px;
    background-color: rgba(119, 255, 0, 0.5);

    &:hover {
      background-color: rgba(119, 255, 0, 1);
    }
  }
}



